import React from "react";
import { createStore } from 'redux';
import homeimage3 from "../Images/homeimage3.png";
import "../CSS/HomeAbout.css";
import Same from "./Same";
import Chat from "./Chat"; // Импортируйте компонент Chat
import '../CSS/Chat.css'

const Home = () => {

    const reducer = (state = {}, action) => {
        switch (action.type) {
            default:
                return state;
        }
    };

// Создание хранилища
    const store = createStore(reducer);

    return (
        <>
            <Same
                title="Sultan Industries"
                text="We are the largest company that provides assistance in many different areas of work. Information network, chemical supply, materials, people, transportation, caskets etc. "
                imgsrc={homeimage3}
                btn={"More About Us"}
            />

            <Chat/>
        </>
    );
};

export default Home;
