import React from "react";
import '../CSS/PolicyStyle.css'


const Policy = () => {

    return (
        <>
            <h1>Policy</h1>
            <div className={"policy"}>
                <ol>
                    <li>"Information Collection: We collect personal information when you use our services, such as your name, contact details, payment information, and preferences. We may also gather data automatically through cookies and similar technologies when you visit our website or use our applications.</li>
                    <li> Use of Information: Your data is used to provide, maintain, and improve our services, including processing transactions, sending updates, and enhancing user experiences. We may also analyze this information to offer personalized content and recommendations.</li>
                    <li>Data Sharing: We may share your data with trusted partners, service providers, or regulatory authorities when necessary to fulfill our services or legal obligations. We never sell your personal information to third parties.</li>
                    <li>Data Security: We employ industry-standard security measures to protect your data from unauthorized access, disclosure, alteration, or destruction. However, please be aware that no data transmission over the internet is entirely secure.</li>
                    <li>Data Retention: We retain your data for as long as necessary to fulfill the purposes outlined in this Privacy Policy or as required by law. You may request the deletion of your account and associated data at any time.</li>
                    <li>Your Choices: You have the right to access, correct, or delete your personal information. You can also opt out of receiving promotional communications from us by following the instructions provided in our messages.</li>
                    <li>Children's Privacy: Our services are not directed at individuals under the age of 13, and we do not knowingly collect their personal information. If you believe we have inadvertently collected such information, please contact us to have it removed.</li>
                    <li>Changes to Policy: This Privacy Policy may be updated from time to time to reflect changes in our practices. We will notify you of significant updates through our website or other communication channels.</li>
                    <li>Contact Us: If you have questions or concerns about your privacy or this policy, please contact our Data Protection Officer at [contact@email.com]. We are committed to addressing your inquiries promptly and professionally.</li>
                </ol>
            </div>
        </>
    )

}
export default Policy;