import React from "react";
import { useState, useEffect } from "react";
import { Provider } from 'react-redux';
import {Route, Routes, useLocation} from 'react-router-dom';
import Home from './Component/Home';
import About from './Component/About';
import Services from "./Component/Services";
import Contact from "./Component/Contact";
import Policy from "./Component/Policy";
import Error from "./Component/Error";
import List from "./List";
import Footer from "./Component/Footer";
import Navigation from './Component/Navigation'
import store from './Component/Store';

const App=()=>{

    const initialState = {
        count: 0,
        user: null,
        // Другие поля состояния
    };

    const [state, setState] = useState(loadStateFromLocalStorage() || initialState);

    function saveStateToLocalStorage(state) {
        try {
            const serializedState = JSON.stringify(state);
            localStorage.setItem('myAppState', serializedState);
        } catch (error) {
            // Обработайте ошибку сохранения состояния
        }
    }

    useEffect(() => {
        saveStateToLocalStorage(state);
    }, [state]);

    function loadStateFromLocalStorage() {
        try {
            const serializedState = localStorage.getItem('myAppState');
            if (serializedState === null) {
                return undefined; // Если состояния нет в localStorage, вернуть undefined
            }
            return JSON.parse(serializedState);
        } catch (error) {
            return undefined; // Если возникает ошибка при загрузке, вернуть undefined
        }
    }


    return(
        <>
            <List/>
            <Navigation />
            <Routes>
                <Route exact path="/" element={<Home />} />
                <Route path="/about" element={<About />}/>
                <Route path="/services" element={<Services />}/>
                <Route path="/contact" element={<Contact />}/>
                <Route path="/policy" element={<Policy />}/>
                {/* Error page */}
                <Route path="*" element={<Error />} />
            </Routes>
            <Footer />

        </>
    )
}

const WrappedApp = () => (
    <Provider store={store}>
        <App />
    </Provider>
);

export default App;
