import React from "react";
import service2 from "../Images/service2.png"
import service1 from "../Images/service1.png"
import service3 from "../Images/service3.png"
import "../CSS/Service.css"
import {Link} from "react-router-dom";

const Services=()=>{

    return(
        <>
            <h1>Services</h1>

            <div className={"services"}>

                <div className={"card"}>
                    <h2>Chemical Assistance</h2>
                    <img src={service1} alt={"service1"} className={"serviceImage"}/>
                    <p>We provide chemical assistance, using our industrial chemical supply.</p>
                    <div className={"btnBox"}>
                        <div className={"btn"}>
                            <Link to={"/about"} className={"readMore"}>Call Dr. White</Link>
                        </div>
                    </div>
                </div>

                <div className={"card"}>
                    <h2>Clue Destroying Assistance</h2>
                    <img src={service2} alt={"service2"} className={"serviceImage"}/>
                    <p>We provide assistance in destroying clues, using our wide spreading network.</p>
                    <div className={"btnBox"}>
                        <div className={"btn"}>
                            <Link to={"/about"} className={"readMore"}>Call Mayor Wayhem</Link>
                        </div>
                    </div>
                </div>

                <div className={"card"}>
                    <h2>Weapon Crafting Assistance</h2>
                    <img src={service3} alt={"service3"} className={"serviceImage"}/>
                    <p>We provide assistance in crafting the most deadliest weapon on Earth, using our access to worlds rarest and finest materials.</p>
                    <div className={"btnBox"}>
                        <div className={"btn"}>
                            <Link to={"/about"} className={"readMore"}>Call Professor Serpinski</Link>
                        </div>
                    </div>
                </div>

            </div>
        </>
    )

}
export default Services;