import React from "react";
import logo1 from './Images/logo1.png'
import {NavLink} from "react-router-dom";
import SearchIcon from '@mui/icons-material/Search';
import PersonIcon from '@mui/icons-material/Person';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import ListStyle from "./CSS/ListStyle.css";

const classNameFunc = ({ isActive }) => (isActive ? "activeItem" : "listItem");

const List = () => {

    return (
        <>
            <header>
                <div className={"container container-flex"}>
                    <div className={"logoContainer"}>
                        <img src={logo1} alt={"logo"} className={"logo"} />
                    </div>

                    <nav>
                        <div className={"list"}>
                            <NavLink to={"/"} className={classNameFunc}>Home</NavLink>
                            <NavLink to={"/about"} className={classNameFunc}>About</NavLink>
                            <NavLink to={"/services"} className={classNameFunc}>Services</NavLink>
                            <NavLink to={"/policy"} className={classNameFunc}>Policy</NavLink>
                            <NavLink to={"/contact"} className={classNameFunc}>Contact</NavLink>

                        </div>
                    </nav>

                    <div className={"icons"}>
                        <SearchIcon className={"icon"}/>
                        <PersonIcon className={"icon"}/>
                        <LocalPhoneIcon className={"icon"}/>
                    </div>
                </div>
            </header>
        </>
    )

}
export default List;